/* ─── Menús overlay ─── */
.menu{
  position: fixed; 
  top: 0; 
  left: 0; 
  width: 100%; 
  height: 100%;
  background: var(--menu-bg); 
  display: flex; 
  flex-direction: column;
  padding: 1rem;
  z-index: 1000;
}

.menu header{ 
  display: flex; 
  justify-content: space-between; 
  align-items: center; 
  margin-bottom: 1rem; 
}

.close-btn{
  background: none; 
  border: none; 
  color: #fff; 
  font-size: 1.5rem; 
  cursor: pointer;
  padding: 0.5rem;
}

.close-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
}

.led-controls {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.led-controls label {
  font-weight: bold;
  margin-bottom: 0.25rem;
}

.led-controls select,
.led-controls input[type="color"] {
  padding: 0.5rem;
  border: none;
  border-radius: 0.3rem;
  font-size: 1rem;
}

.led-controls input[type="color"] {
  width: 100%;
  height: 3rem;
  cursor: pointer;
}

.led-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.menu-btn{ 
  background: var(--light-gray); 
  color: #222; 
  border: none; 
  padding: .75rem 1rem; 
  border-radius: .3rem;
  font-size: 1rem;
  cursor: pointer;
  flex: 1;
}

.menu-btn:hover {
  filter: brightness(1.1);
}

.menu-btn:active {
  transform: translateY(1px);
}
